.movie-card {
  border: 2px solid black;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 400px;
  width: 250px;
  margin: 5px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  overflow: hidden; }
  .movie-card .dark-background {
    background-color: #282935;
    height: 400px;
    width: 250px;
    opacity: .03;
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
    position: absolute; }
  .movie-card .movie-title-container {
    color: white;
    -webkit-transform: translateY(-400px);
            transform: translateY(-400px);
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
    margin: 8px 10px; }
  .movie-card .overview {
    color: white;
    position: relative;
    -webkit-transform: translateY(400px);
            transform: translateY(400px);
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
    margin: 10px;
    max-height: 200px;
    overflow: scroll; }
  .movie-card:hover .dark-background {
    cursor: pointer;
    opacity: .90; }
  .movie-card:hover .overview {
    -webkit-transform: translateY(0px);
            transform: translateY(0px); }
  .movie-card:hover .movie-title-container {
    -webkit-transform: translateY(0px);
            transform: translateY(0px); }

img {
